import Artplayer from 'artplayer';
import mpegts from 'mpegts.js';
import Danmaku from 'danmaku';

import ploading from "./img/ploading.gif";
import state from "./img/state.png";
import indicator from "./img/indicator.svg";

(() => {
    let player,
        danmaku,
        hadPause = true,
        config = {
            container: '.artplayer-app',
            url: "/stream?_=" + new Date().getTime(),
            title: "" + new Date().getTime(),
            type: "flv",
            volume: 0.5,
            isLive: true,
            muted: false,
            autoplay: true,
            pip: true,
            autoSize: true,
            autoMini: true,
            screenshot: true,
            setting: true,
            loop: false,
            flip: true,
            playbackRate: true,
            aspectRatio: true,
            fullscreen: true,
            fullscreenWeb: true,
            subtitleOffset: true,
            miniProgressBar: true,
            mutex: true,
            backdrop: true,
            playsInline: true,
            autoPlayback: true,
            theme: '#23ade5',
            lang: navigator.language.toLowerCase(),
            whitelist: ['*'],
            moreVideoAttr: {
                crossOrigin: 'anonymous',
            },
            settings: [],
            contextmenu: [],
            layers: [],
            quality: [],
            thumbnails: {},
            subtitle: {},
            highlight: [],
            controls: [],
            icons: {
                loading: '<img src=' + ploading + '>',
                state: '<img width="150" heigth="150" src=' + state + '>',
                indicator: '<img width="16" heigth="16" src=' + indicator + '>',
            },
            customType: {
                flv: function (video, url) {
                    if (mpegts.getFeatureList().mseLivePlayback) {
                        var player = mpegts.createPlayer({
                            type: 'flv',  // could also be mpegts, m2ts, flv
                            isLive: true,
                            url: url
                        });
                        player.attachMediaElement(video);
                        player.load();
                        player.on("error", function(){
                            player.destroy();
                            var c = config;
                            c.type="mp4";
                            initPlay(c);
                        });
                    }
                },
            },
        };
    
    function initPlay(config) {
        if(player != undefined && player.destroy != undefined)player.destroy();
        player = new Artplayer(config);
        player.on('play', (...args) => {
            if (hadPause) player.switchUrl("/stream?_=" + new Date().getTime(), new Date().getTime());
            hadPause = false;
        });
        player.on('pause', (...args) => {
            hadPause = true;
        });
        if(danmaku != undefined && danmaku.destroy != undefined)danmaku.destroy();
        danmaku = new Danmaku({
            container: document.getElementsByClassName('art-danmuku')[0],
        });
    }initPlay(config);
    /**
     * ws 
     */
     function ws() {
        if (window["WebSocket"]) {
            setInterval(function () { 
                if(danmaku != undefined && danmaku.resize != undefined)danmaku.resize()
             }, 1000)
            var conn = new WebSocket("ws://" + window.location.host + "/ws");
            conn.onopen = function () {
                setInterval(function () {
                    conn.send("test")
                }, 1000)
            }
            conn.onclose = function () {
                ws()
            };
            conn.onmessage = function (evt) {
                try {
                    let data = JSON.parse(evt.data)
                    danmaku.emit(data)
                } catch (e) {
                    console.log(e)
                    console.log(evt.data)
                }
            };
        }
    } if(window.location.pathname=="/now/")ws();
})();
